.keyboard{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.8rem;
    margin-top: 2rem;
    &__row{
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.8rem;
    }
}

.key{
    width: 7rem;
    height: 7rem;
    font-weight: bold;
    font-size: 3rem;

    &:active{
        background-color: rgb(173, 173, 173);
    }
}


.enter{
    width: 15rem;
}

.backspace{
    width: 15rem;
}